.breadcrumbs{
  padding: 1.5em 2em;
  border-radius: 1rem;
  border: 1px solid $gray;
  background: #FFF;
  box-shadow: 12px 9px 28px -11px rgba(61, 109, 151, 0.21);
  position: relative;
  z-index: 2;
  &__items{
    margin-bottom: 1em;
  }
  &__item {
    font-size: 1em;
    line-height: 150%;
    position: relative;
    font-weight: 400;
    display: inline;
    color: #172C3E;
    &:not(:last-child) {
      margin-right: 1em;
      padding-right: 1.5em;
      &:after {
        content: '';
        position: absolute;
        z-index: 1;
        width: 1em;
        height: 1em;
        background-image: url(../img/bread-arrow.svg);
        @include bgImgSize(100%);
        top: .125em;
        right: 0;
        margin-left: .5em;
      }
    }
  }
  .text32{
    text-transform: uppercase;
  }
}


@media screen and (max-width: 992px){
  .breadcrumbs{
    padding: 1em 1.5em;
    &__items{
      margin-bottom: .5em;
    }
    .text32{
      font-size: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .breadcrumbs{
    padding: .5em 1.5em;
    &__item{
      &:not(:last-child){
        margin-right: .25em;
        padding-right: 1em;
      }
    }
    .text32{
      font-size: 1.25em;
    }
  }
}
