.header{
  padding: 1.5em 0 7.5em;
  background-image: url(../img/header-bg.webp);
  background-size: cover;
  background-position: center;
  color: #fff;
  &__inner{
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  &__logo{
    font-weight: 900;
    text-transform: uppercase;
  }
  &__desc{
    width: 20em;
    margin-top: .5em;
  }
  &__middle{
    display: flex;
    align-items: center;
    padding-top: 1.25em;
  }
  &__right{
    padding-top: 1.44em;
    display: flex;
    align-items: flex-start;
  }
  a{
    &:hover{
      color: $secondary;
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    padding: 0 1em;
    margin-right: .5em;
    font-size: 1em;
    height: 2.5em;
    @include center;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      left: .5em;
      width: calc(100% - 1em);
      height: .125em;
      background-color: #fff;
      border-radius: .5em;
      opacity: 0;
      transition: all .3s;
    }
    &._active{
      &:before{
        opacity: 1;
      }
    }
  }
}

.burger{
  width: 2.425em;
  svg,
  img{
    width: 100%;
    height: auto;
    display: block;
  }
  &:hover{
    path{
      stroke: $secondary;
    }
  }
}

.header-contacts{
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.header-social{
  display: flex;
  align-items: center;
  &__item{
    margin-right: 1em;
    width: 1.5em;
    flex-shrink: 0;
    &:hover{
      opacity: .8;
    }
    img{
      width: 100%;
      display: block;
    }
  }
}

.header-menu{
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  z-index: 100;
  position: relative;
  &._open{
    opacity: 1;
    pointer-events: unset;
  }
  &__close{
    position: absolute;
    width: 2.5em;
    top: 0;
    right: 100%;
    opacity: .6;
    &:hover{
      opacity: 1;
    }
    img{
      width: 100%;
      display: block;
    }
  }
  &__wrap{
    width: 22em;
    height: $fullVH;
    position: fixed;
    z-index: 101;
    top: 0;
    right: 0;
  }
  &__bg{
    background: rgba(13, 26, 38, 0.79);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $fullVH;
    z-index: 100;
  }
  &__scroll{
    height: 100%;
    overflow-y: auto;
    padding: 1.5em 1.5em 3em;
    background-color: #fff;
  }
  .nav{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    font-family: $font2;
    &__link{
      color: $dark;
      @include lastM(0 0 .5em);
      &:before{
        background: $dark;
      }
    }
  }
  .menu{
    margin-top: 1.125em;
  }
}

.menu-contacts{
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: 2em;
  .header-social{
    margin: 1em 0;
    justify-content: center;
    &__item{
      margin: 0 .5em;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding-bottom: 9.25em;
    background-image: url(../img/header-bg2.webp);
    &._type2{
      padding-bottom: 6.75em;
      &~.content{
        .catalog{
          margin-top: -4.875em;
        }
      }
    }
    &__logo{
      font-size: 1.5em;
    }
    &__inner{
      justify-content: flex-end;
      padding: 0 1em;
    }
    &__desc{
      font-size: .75em;
      margin-top: .675em;
    }
    &__left{
      margin-right: auto;
    }
    &__middle{
      padding-top: 0;
      order: 2;
      align-items: flex-start;
      margin-left: 2em;
    }
    &__right{
      padding-top: .3em;
    }
  }

  .nav{
    display: none;
  }

  .burger{
    width: 2.5em;
  }

  .header-menu{
    &__wrap{
      width: 20em;
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    padding: .5em 0 10.25em;
    &._type2{
      padding-bottom: 5.25em;
      &~.content{
        .catalog{
          margin-top: -3.8125em;
        }
      }
    }
    &__inner{
      padding: 0;
    }
    &__right{
      display: none;
    }
  }
}
