.fg {
  position: relative;
  margin: 0;
  input,
  textarea{
    border: 1px solid #F9F9F9;
    border-radius: 0.5em;
    background: #F9F9F9;
    padding: 0 1em;
    height: 4em;
    width: 100%;
    font-size: 1em;
    line-height: 150%;
    color: $black;
    &::placeholder {
      color: #838383;
    }
    &:focus {
      border-color: $primary;
    }
  }
  textarea{
    padding: 1em;
    height: 10em;
    resize: none;
  }
}

.m-check,
.m-switch{
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check {
  label {
    padding: 0 0 0 2.5em;
    position: relative;
    min-height: 1.5em;
    cursor: pointer;
    display: block;
    font-size: 1em;
    line-height: 150%;
    color: $dark2;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      background-color: #fff;
      width: 1.5em;
      height: 1.5em;
      left: 0;
      top: 0;
      transition: all .100s;
      border: 1px solid $gray;
    }
  }
}

.m-check{
  input{
    &:checked+label{
      color: $primary;
      &:before{
        background-image: url(../img/check.svg);
        border-color: $primary;
      }
    }
  }
  label{
    &:before{
      border-radius: .5em;
      @include bgImgSize(.625em);
    }
  }
}
