.contacts-page{
  padding-bottom: 3.5em;
  &__grid{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: calc(100% - 35em) 32.5em;
    gap: 2.5em;
  }
}

.contacts-info{
  max-width: 43em;
  padding: 1.5em 0 1.5em 2.5em;
}

.contacts-group{
  @include lastM(0 0 5em);
  display: grid;
  align-items: center;
  grid-template-columns: 13.875em calc(100% - 16.375em);
  gap: 2.5em;
  &__info{
    @include grid100gap(.5em);
  }
  &__city{
    text-transform: uppercase;
  }
  &__items{
    padding-left: 2.5em;
    border-left: 1px solid $gray;
  }
}

.contact-item{
  @include grid100gap(.5em);
  @include lastM(0 0 2.5em);
}

.contacts-form{
  padding: 2.88em 4em;
  border-radius: 0.5rem;
  background: #fff;
  box-shadow: 25px 34px 103px -25px rgba(0, 0, 0, 0.10);
  &__top{
    @include grid100gap(.5em);
    .text16{
      color: #6D6D6D;
    }
  }
  .text32{
    text-transform: uppercase;
  }
  &__fields{
    margin-top: 1.5em;
    @include grid100gap(.5em);
  }
  &__nav{
    display: flex;
    margin-top: 1.5em;
    .m-btn{
      border-radius: .5em;
      font-family: $font2;
      font-weight: 800;
      text-transform: uppercase;
      span{

      }
    }
  }
}


@media screen and (max-width: 992px){
  .contacts-page{
    padding-bottom: 4.5em;
    &__grid{
      grid-template-columns: 100%;
      margin-top: 1.5em;
    }
  }

  .contacts-info{
    padding: 0;
  }

  .contacts-form{
    padding-left: 5em;
    padding-right: 5em;
  }

  .contacts-group{
    @include lastM(0 0 2.5em);
  }
}


@media screen and (max-width: 576px){
  .contacts-page{
    &__grid{
      margin-top: 3em;
    }
  }

  .contacts-form{
    padding-left: 2.5em;
    padding-right: 2.5em;
    &__top{
      text-align: center;
    }
  }

  .contacts-group{
    grid-template-columns: 100%;
    gap: 1em;
    &__items{
      padding-left: 1.5em;
    }
  }

  .contact-item{
    @include lastM(0 0 1em);
  }
}
