.about-company{
  padding-bottom: 3.5em;
  &__grid{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: 13.5em calc(100% - 16em);
    gap: 1em 2.5em;
  }
  &__info{
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-right: 1px solid $gray;
  }
  &__logo{
    display: block;
    width: 9.7em;
    margin-bottom: .5em;
  }
  &__media{
    margin-top: 5em;
    display: grid;
    grid-template-columns: 42.5em calc(100% - 45em);
    gap: 5em 2.5em;
  }
  &__title{
    text-transform: uppercase;
    margin-bottom: 1em;
  }
}

.gallery2{
  &__big{
    .gallery__img{
      @include imgCover(65.89%);
    }
  }
  &__small{
    margin-top: 1em;
    padding: 0 3.67em;
    position: relative;
    .swiper{
      &-container{
        overflow: hidden;
      }
      &-slide{
        padding-right: .67em;
        width: 7.17em;
        &-thumb-active{
          .gallery__img{
            border-color: $primary;
            box-shadow: 0px 10px 25px -4px rgba(90, 160, 221, 0.37);
          }
        }
      }
      &-button{
        width: 3em;
        img{
          width: 100%;
          display: block;
        }
        &-prev{
          left: 0;
        }
        &-next{
          right: 0;
        }
      }
    }
    .gallery__img{
      @include imgCover(84.6%);
      border-radius: 1em;
      border: 4px solid #FFF;
      box-shadow: 0px 4px 13px -3px rgba(0, 0, 0, 0.16);
    }
  }
}

.video{
  display: block;
  position: relative;
  &__play{
    @include centerAbsolute;
    width: 5em;
  }
  &__img{
    @include imgCover(70%);
    img{
      border-radius: 1em;
    }
  }
}

.pdf-catalog{
  margin-top: 2.5em;
  @include grid100gap(1em);
  &__grid{
    margin-top: -1.5em;
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + 1.5em);
  }
  &__item{
    display: block;
    width: calc(100% / 7 - 1.5em);
    margin: 1.5em 1.5em 0 0;
  }
  &__img{
    @include imgCover(92.1%);
    border-radius: 1em;
    img{
      border-radius: inherit;
      border: 4px solid #fff;
      box-shadow: 0px 10px 25px -4px rgba(198, 198, 198, 0.37);
      transition: all .3s;
    }
    &:hover{
      img{
        border-color: $primary;
        box-shadow: 0px 10px 25px -4px rgba(90, 160, 221, 0.37);
      }
    }
  }
}

.about-page{
  padding-bottom: 3.5em;
  &__top{
    margin-top: 2.5em;
    display: flex;
    align-items: center;
  }
  &__logo{
    text-transform: uppercase;
  }
  &__desc{
    margin-left: 2em;
    padding-left: 2em;
    border-left: 1px solid $gray;
    min-height: 1.875em;
    display: flex;
    align-items: center;
  }
  &__grid{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 2.5em;
  }
  &__img{
    width: 100%;
    display: block;
    border-radius: 1em;
  }
  .text{
    p{
      @include lastM(0 0 1.5em);
    }
  }
}


@media screen and (max-width: 992px){
  .about-company{
    padding-bottom: 4.5em;
    &__media{
      margin-top: 2.5em;
      grid-template-columns: 100%;
    }
  }

  .gallery2{
    &__big{
      .gallery__img{
        padding-bottom: 60.8%;
      }
    }
  }

  .video{
    &__img{
      padding-bottom: 60.8%;
    }
  }

  .pdf-catalog{
    &__item{
      width: 9em;
    }
    &__img{
      padding-bottom: 111%;
    }
  }

  .about-page{
    padding-bottom: 4.5em;
    &__top{
      margin-top: 1.5em;
    }
    &__grid{
      grid-template-columns: 100%;
      margin-top: 1em;
    }
  }
}


@media screen and (max-width: 992px){
  .about-company{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .gallery2{
    &__big{
      .gallery__img{
        padding-bottom: 73.1%;
      }
    }
    &__small{
      padding: 0;
      .swiper{
        &-button{
          display: none;
        }
        &-slide{
          width: 6.5em;
          padding: 0;
          &:not(.swiper-slide-active){
            margin-left: -3.5em;
          }
        }
      }
    }
  }

  .video{
    &__img{
      padding-bottom: 73.1%;
    }
  }
}
