.text32{
  font-size: 2em;
  line-height: 125%;
}

.text24{
  font-size: 1.5em;
  line-height: 125%;
}


.text h3,
.text20{
  font-size: 1.25em;
  line-height: 130%;
}

.text p,
.text16{
  font-size: 1em;
  line-height: 125%;
}

.text12{
  font-size: .75em;
  line-height: 125%;
}

.lh150{
  line-height: 150%;
}

.extra-bold-text{
  font-weight: 800;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.font2{
  font-family: $font2;
}

.primary-text{
  color: $primary;
}

.dark-text2{
  color: $dark;
}

.dark-text2{
  color: $dark2;
}

.underline-text{
  text-decoration: underline;
}


@media screen and (max-width: 576px){
  .text32{
    font-size: 1.5em;
  }
}
