@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.5em;
    height: 3.25em;
    border-radius: 1em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
        line-height: 120%;
    }
}

.m-btn-primary{
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: darken($primary,10%);
        color: #fff;
    }
}

.m-btn-gray-outline{
    color: $primary;
    border: 1px solid $gray;
    &:hover{
        border-color: darken($primary,10%);
        background-color: darken($primary,10%);
        color: #fff;
    }
}
