.map{
  height: 30em;
}

.footer-bottom{
  padding: 5em 0;
  background-image: url(../img/flag.webp);
  background-size: cover;
  background-position: center;
}

.footer{
  margin-top: 3.25em;
  position: relative;
  z-index: 1;
  &__inner{
    position: relative;
    padding: 0 2.5em;
    display: flex;
    align-items: stretch;
    justify-content: space-between;
  }
  &__logo{
    font-weight: 900;
    text-transform: uppercase;
  }
  &__desc{
    max-width: 20em;
    margin-top: .5em;
  }
  &__right{
    padding: .5em 0;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-between;
  }
}

.footer-nav{
  position: absolute;
  padding: 1.5em 2.5em;
  width: 100%;
  left: 0;
  top: -9.8em;
  box-shadow: 0px 53px 49px -47px rgba(12, 95, 168, 0.76);
  background-color: $primary;
  z-index: 3;
  border-radius: 1em;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__links{
    display: flex;
    align-items: center;
  }
  &__link{
    padding: 0 1em;
    margin-right: 1em;
    font-size: 1em;
    height: 2.5em;
    @include center;
  }
  &__right{
    display: flex;
    align-items: center;
  }
  a{
    &:hover{
      color: $secondary;
    }
  }
}

.social,
.contacts{
  display: flex;
  &__item{
    display: flex;
    align-items: center;
    span{
      font-size: 1em;
      line-height: 125%;
    }
    img{
      width: 1.5em;
      flex-shrink: 0;
      display: block;
      margin-right: .5em;
    }
  }
}

.social{
  align-items: center;
  &__item{
    @include lastM(0 1.5em 0 0);
  }
}

.contacts{
  flex-direction: column;
  align-items: flex-start;
  margin-left: 2.5em;
  &__item{
    @include lastM(0 0 1.5em 0);
  }
}

.scroll-top{
  box-shadow: .6875em .6875em .75em 0 rgba(255, 197, 47, 0.25);
  position: absolute;
  right: 0;
  top: -35em;
  transform: translateY(-50%);
  background-color: $secondary;
  border-radius: .5em;
  width: 4em;
  height: 4em;
  @include center;
  &:hover{
    background-color: darken($secondary,10%);
  }
  img{
    width: 1.5em;
  }
}



@media screen and (max-width: 992px){
  .map{
    display: none;
  }

  .footer-bottom{
    padding: 8.75em 0 5.6em;
    position: relative;
    background-image: url(../img/flag3.webp);
    background-position: center bottom;
  }

  .footer{
    margin-top: 4.25em;
    &__inner{
      padding: 0;
      position: static;
    }
    &__left{
      display: none;
    }
    &__right{
      padding: 0;
      width: 100%;
      align-items: center;
      flex-direction: row;
    }
  }

  .footer-nav{
    top: -1.5em;
    padding: 1.5em 2.75em;
    &__link{
      display: none;
    }
    &__right{
      width: 100%;
      justify-content: space-between;
    }
  }

  .contacts{
    &__item{
      @include lastM(0 0 1em);
    }
  }

  .scroll-top{
    top: -1.5em;
    right: 50%;
    transform: translate(50%,-50%);
    z-index: 10;
  }
}


@media screen and (max-width: 576px){
  .footer-bottom{
    padding: 14em 0 .94em;
    background-image: url(../img/flag2.webp);
  }

  .footer{
    &__right{
      flex-direction: column;
      text-align: center;
      .text12{
        font-weight: 300;
      }
    }
    &__c{
      margin-top: 1.25em;
      color: #fff;
    }
  }

  .footer-nav{
    border-radius: 0;
    top: 0;
    padding: .5em .5em .94em;
    &__right{
      flex-direction: column;
    }
  }

  .contacts{
    margin: .5em 0 0;
    align-items: center;
    &__item{
      @include lastM(0 0 .5em);
    }
  }

  .scroll-top{
    top: -4.5em;
    transform: translate(50%,0);
  }
}
