.products{
  padding-left: 2em;
  padding-right: 2em;
  margin-top: 2.5em;
  position: relative;
  z-index: 2;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
    gap: 4em 2.5em;
    margin-top: 4em;
  }
}

.filter{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.filter-item{
  position: relative;
  &__show{
    padding: 0 1.5em;
    height: 3.125em;
    @include center;
    background-color: $primary;
    color: #fff;
    border-radius: 1em;
    font-family: $font2;
    transition: all .3s;
    cursor: pointer;
    &:hover{
      background-color: darken($primary,10%);
    }
    span{
      font-size: 1em;
      line-height: 120%;
    }
    img{
      width: 1em;
      flex-shrink: 0;
      margin-left: 1em;
    }
  }
  &__hidden{
    position: absolute;
    top: 100%;
    left: 0;
    margin-top: .5em;
    padding: 2.5em;
    border-radius: 1rem;
    border: 2px solid $primary;
    background: #fff;
    width: 19.0625em;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    z-index: 10;
  }
  &._type2{
    .filter-item__show{
      border: 1px solid $gray;
      background-color: transparent;
      color: $primary;
      &:hover{
        background-color: $gray;
      }
    }
    @media screen and (min-width: 577px){
      .filter-item__hidden{
        left: unset;
        right: 0;
      }
    }
  }
  &._active{
    .filter-item__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.filter-group{
  @include lastM(0 0 2em);
  &__top{
    margin-bottom: 1em;
    color: $dark;
  }
  &__items{
    @include grid100gap(1em);
  }
}

.product-card{
  display: block;
  &:hover{
    .product-card{
      color: $primary;
      &__top{
        &:before{
          background-color: $primary;
        }
      }
      &__img{
        img{
          box-shadow: 8px 11px 27px -14px rgba(24, 59, 91, 0.57);
        }
      }
    }
  }
  &__top{
    position: relative;
    &:before{
      content: '';
      position: absolute;
      height: calc(100% + 1em);
      width: calc(100% - 4.375em);
      top: -1.5em;
      right: -1.125em;
      background-color: #EEE;
      border-radius: 1em;
      transition: all .3s;
    }
  }
  &__img{
    z-index: 2;
    @include imgCover(97.2%);
    img{
      border-radius: 1em;
      transition: all .3s;
    }
  }
  &__content{
    padding: 1em 1.5em 0;
  }
}

.pagination{
  margin-top: 5em;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__items{
    display: flex;
    justify-content: center;
  }
  &__item{
    @include center;
    width: 2.625em;
    height: 2.75em;
    margin: 0 .25em;
    border-radius: 0.5em;
    border: 1px solid $gray;
    background: #fff;
    box-shadow: 15px 15px 30px -14px rgba(0, 0, 0, 0.15);
    color: $dark2;
    font-size: 1em;
    line-height: 150%;
    &:hover,
    &._active{
      color: $dark2;
      border-color: $primary;
      box-shadow: 0px 15px 30px -14px #0E74CE;
    }
  }
  &__more{
    margin-top: 2.5em;
    font-weight: 800;
    text-transform: uppercase;
    font-family: $font2;
    border-radius: .5em;
    height: 2.75em;
    span{
      font-size: .75em;
    }
    &:hover{
      path{
        stroke: #fff;
      }
    }
    svg{
      width: 1em;
      margin-left: .5em;
    }
  }
}


@media screen and (max-width: 992px){
  .products{
    margin-top: 1.5em;
    padding-left: 1.5em;
    padding-right: 1.5em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 1.25em));
    }
  }

  .pagination{
    margin-top: 4em;
  }
}


@media screen and (max-width: 576px){
  .filter{
    flex-direction: column;
    align-items: center;
    padding: 0;
  }

  .filter-item{
    @include lastM(0 0 .5em);
    &__hidden{
      left: 50%;
      transform: translateX(-50%);
    }
  }

  .products{
    padding-left: 0;
    padding-right: 0;
    &__grid{
      grid-template-columns: 100%;
      gap: 3em;
      margin-top: 3em;
      padding-left: 1.12em;
      padding-right: 2.3em;
    }
  }

  .pagination{
    margin-top: 3em;
  }
}
