.catalog{
  padding-bottom: 3.25em;
  margin-top: -5em;
  position: relative;
  z-index: 3;
  &__inner{
    display: grid;
    grid-template-columns: 20em calc(100% - 21.6em);
    gap: 1.6em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 80%;
      height: 5em;
      border-radius: 1em 1em 0 0;
      background-color: #fff;
    }
  }
  &__sidebar{
    padding: 1.5em 0 0 1.5em;
  }
}

.menu-group{
  @include lastM(0 0 1.75em);
  &__top{
    padding: 1em;
    text-transform: uppercase;
    font-weight: 800;
    color: #fff;
    min-height: 3.5625em;
    border-radius: 1em;
    position: relative;
    img{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
    span{
      position: relative;
      z-index: 2;
    }
    &._gray{
      color: $black;
    }
  }
  &__links{
    margin-top: .5em;
    padding: .625em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    font-size: 1em;
    line-height: 125%;
    @include lastM(0 0 .5em);
  }
}

.main-slider{
  overflow: hidden;
  position: relative;
  &__img{
    @include imgCover(34%);
    overflow: hidden;
    img{
      mask: url(#mask);
      -webkit-mask: url(../img/mask.svg) top left / cover;
    }
  }
  .swiper{
    &-nav{
      position: absolute;
      right: .5em;
      bottom: .5em;
      display: flex;
      z-index: 2;
    }
    &-button{
      margin: 0 0 0 .5em;
      position: static;
      border-radius: 1em;
      background: #FFF;
      width: 3.5em;
      height: 3.5em;
      @include center;
      svg{
        width: 1.5em;
        height: auto;
      }
      &:hover{
        background: $primary;
        path{
          stroke: #fff;
        }
      }
    }
  }
}

.advantages{
  margin-top: 1.5em;
  padding-left: 2em;
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 3.6em / 3));
  gap: 1.8em;
  border-top: 1px solid #F2F2F2;
}

.advantage-card{
  padding-top: 1em;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__img{
    position: relative;
    height: 5em;
    margin-bottom: 1.5em;
    img{
      height: 100%;
      width: auto;
      display: block;
      position: relative;
      z-index: 2;
    }
  }
  &__text{
    line-height: 150%;
  }
  &__decor{
    position: absolute;
    top: -.7em;
    right: 0;
    border-radius: 1em 1em 1em 2.5em;
    opacity: 0.20000000298023224;
    background: $primary;
    width: 3.5em;
    height: 3.5em;
    &._gray{
      background-color: #172C3E;
      opacity: .1;
      border-radius: 2.5em 1em 1em 1em;
      top: unset;
      bottom: -.56em;
      right: -.21em;
    }
    &._yellow{
      background-color: $secondary;
      border-radius: 1em 2.5em 1em 1em;
      top: unset;
      bottom: -.25em;
      right: unset;
      left: -.58em;
    }
  }
}

.about{
  margin-top: 7em;
  padding: .5em;
  border-radius: 1em;
  border: 1px solid $gray;
  display: grid;
  grid-template-columns: calc(100% - 22.8125em - 3.7em) 22.8125em;
  gap: 3.7em;
  &__img{
    img{
      width: 100%;
      height: 26.125em;
      object-fit: cover;
      display: block;
      mask: url(../img/mask3.svg) top left / cover;
      -webkit-mask: url(../img/mask3.svg) top left / cover;
    }
  }
  &__content{
    padding: 2.1em 0 2.1em 2.5em;
  }
  &__info{
    @include grid100gap(1em);
  }
  &__nav{
    display: flex;
  }
}

.team{
  margin-top: 6em;
  padding-left: 3em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.86em / 3));
    gap: 1.43em;
    margin-top: 2.5em;
  }
}

.team-card{
  &__top{
    padding-top: 1.12em;
    position: relative;
  }
  &__rect{
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    display: block;
    &._view2,
    &._view3{
      display: none;
    }
  }
  &__img{
    height: 19.25438em;
    position: relative;
    z-index: 2;
    img{
      height: 100%;
      width: auto;
      display: block;
    }
  }
  &__content{
    padding: 1.5em 1.5em 0;
    @include grid100gap(.5em);
  }
}



@media screen and (max-width: 992px){
  .catalog{
    padding-bottom: 4.25em;
    margin-top: -6.75em;
    &__inner{
      grid-template-columns: 100%;
      gap: 0;
      &:before{
        display: none;
      }
    }
    &__sidebar{
      display: none;
    }
  }

  .main-slider{
    &__img{
      overflow: hidden;
      border-radius: 1.25em;
      img{
        -webkit-mask: url(../img/mask2.svg) top left / cover;
        //clip-path: polygon(10% 0, 100% 0, 100% 100%, 0% 100%);
      }
    }
  }

  .advantages{
    padding-left: 0;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
    gap: 1.5em;
  }

  .advantage-card{
    &__text{
      br{
        display: none;
      }
    }
  }

  .about{
    margin-top: 2em;
    padding: 2.5em 1.5em;
    grid-template-columns: 100%;
    gap: 2.5em;
    &__content{
      padding: 0;
    }
    &__img{
      img{
        height: 15em;
        border-radius: 1.25em;
        mask: url(../img/mask4.svg) top left / cover;
        -webkit-mask: url(../img/mask4.svg) top left / cover;
      }
    }
  }

  .team{
    margin-top: 3em;
    padding-left: 0;
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
      gap: 1em;
    }
  }

  .team-card{
    &__top{
      padding-top: .96em;
    }
    &__img{
      height: 16.5375em;
      &._view1{
        display: none;
      }
      &._view2{
        display: block;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .catalog{
    margin-top: -8.75em;
  }

  .main-slider{
    &__img{
      padding-bottom: 69.7%;
    }
  }

  .advantages{
    grid-template-columns: 100%;
    gap: 1.25em;
    padding-top: 1em;
  }

  .advantage-card{
    padding: 0;
    &__img{
      height: 4.5em;
      margin-bottom: 1em;
    }
  }

  .about{
    &__img{
      img{
        mask: url(../img/mask2.svg) top left / cover;
        -webkit-mask: url(../img/mask2.svg) top left / cover;
      }
    }
  }

  .team{
    padding-left: 1.5em;
    padding-right: 1.5em;
    &__grid{
      grid-template-columns: 100%;
      gap: 2.5em;
    }
  }

  .team-card{
    &__top{
      padding-top: .83em;
    }
    &__img{
      height: 14.175em;
      &._view2{
        display: none;
      }
      &._view3{
        display: block;
      }
    }
    &__content{
      padding-top: .5em;
    }
  }
}
