._first-section{
  margin-top: -5em;
}

.catalog-item{
  &__grid{
    display: grid;
    grid-template-columns: 46.25em calc(100% - 50.3em);
    gap: 4.05em;
    margin-top: 3.5em;
  }
}

.gallery{
  display: flex;
  height: 23em;
  &__small,
  &__big{
    height: 100%;
  }
  &__small{
    width: 5em;
    flex-shrink: 0;
    margin-right: 1em;
    padding: 2.25em 0;
    position: relative;
    .swiper{
      height: calc(100% + 1em);
      overflow: unset;
      &-slide{
        opacity: 0;
        pointer-events: none;
        position: relative;
        height: calc(100% / 3);
        padding-bottom: 1em;
        &-active,
        &-next,
        &-next+.swiper-slide{
          opacity: 1;
          pointer-events: unset;
        }
        .gallery__img{
          border-radius: 1em;
          border: 4px solid #FFF;
          box-shadow: 0px 4px 13px -3px rgba(0, 0, 0, 0.16);
        }
        &-thumb-active{
          .gallery__img{
            border-color: $primary;
            box-shadow: 0px 10px 25px -4px rgba(90, 160, 221, 0.37);
          }
        }
      }
    }
  }
  &__big{
    position: relative;
    width: calc(100% - 6em);
  }
  .swiper{
    width: 100%;
    height: 100%;
    &-slide{
      width: 100%;
      a{
        display: block;
        height: 100%;
      }
    }
    &-button{
      &-next,
      &-prev{
        left: 50%;
        transform: translateX(-50%);
        margin: 0;
        width: 1.5em;
        height: 1.5em;
        img{
          width: 100%;
          display: block;
        }
      }
      &-prev{
        top: 0;
      }
      &-next{
        top: unset;
        bottom: 0;
      }
    }
  }
  &__img{
    position: relative;
    height: 100%;
    display: block;
    img{
      position: absolute;
      height: 100%;
      width: 100%;
      top: 0;
      left: 0;
      object-fit: cover;
      border-radius: 1em;
    }
  }
  &__loupe{
    position: absolute;
    right: .5em;
    bottom: .5em;
    width: 3em;
    z-index: 2;
  }
}

.catalog-info{
  max-width: 26em;
  @include grid100gap(2.5em);
  &__hint{
    display: flex;
    padding-left: .375em;
    &:before{
      content: '';
      flex-shrink: 0;
      width: .25em;
      height: .25em;
      border-radius: 50%;
      margin-right: .5em;
      margin-top: .375em;
      background-color: $dark;
    }
  }
}

.props{
  @include grid100gap(1em);
  &__row{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
  }
}

.catalog-detail{
  padding: 3.5em 0;
  &__content{
    margin-top: 2em;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 3em;
    .pagination__more{
      margin: 0;
    }
  }
}

.tabs{
  padding: 1.5em 2.5em;
  display: flex;
  align-items: center;
  border-radius: 1em;
  border: 1px solid $gray;
  background: #FFF;
  box-shadow: 12px 9px 28px -11px rgba(61, 109, 151, 0.21);
}

._tab{
  display: flex;
  align-items: center;
  font-family: $font2;
  font-weight: 800;
  text-transform: uppercase;
  cursor: pointer;
  &:hover,
  &._active{
    color: $primary;
  }
  &._active{
    pointer-events: none;
  }
  span{
    font-size: 1.25em;
    line-height: 125%;
  }
  &:not(:last-child){
    &:after{
      content: '';
      background-image: url(../img/line.svg);
      @include bgImgSize(100%);
      width: .5em;
      height: 1.5625em;
      flex-shrink: 0;
      margin: 0 3em;
      pointer-events: none;
    }
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.text{
  h3{
    font-weight: 700;
  }
  hr{
    height: 1em;
    display: block;
    border: none;
  }
  h3{
    @include lastM(0 0 .8em);
  }
  p,ul{
    line-height: 150%;
    @include lastM(0 0 1em);
  }
  ul{
    padding-left: .625em;
    @include grid100gap(.0625em);
    li{
      padding-left: 1em;
      font-size: 1em;
      line-height: 150%;
      position: relative;
      &:before{
        content: '';
        position: absolute;
        width: .375em;
        height: .375em;
        border-radius: 50%;
        background-color: $black;
        left: 0;
        top: .5625em;
      }
    }
  }
}

.product-props{
  padding: 0 2.5em;
  display: flex;
  justify-content: space-between;
  &__group{
    width: 30.5em;
    flex-shrink: 0;
  }
  &__title{
    margin-bottom: 1em;
  }
}

.product-prop{
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  @include lastM(0 0 1em);
  &:before{
    content: '';
    flex-grow: 1;
    min-width: 1em;
    border-bottom: 1px dashed $gray;
    height: 100%;
    margin: 0 .5em;
    flex-shrink: 0;
    transform: translateY(-.25em);
  }
  &__title,
  &__desc{
    font-size: 1em;
    line-height: 150%;
  }
  &__title{
    color: $dark;
    order: -1;
  }
  &__desc{
    color: $dark2;
    font-family: $font2;
    font-weight: 600;
    text-align: right;
    a{
      color: $primary;
      &:hover{
        color: $secondary;
      }
    }
  }
}

.product-slider{
  padding-left: 1.5em;
  padding-right: 2.63em;
  position: relative;
  .swiper{
    &-container{
      width: calc(100% + 2.96em);
      padding-top: 1.5em;
      overflow: hidden;
    }
    &-slide{
      width: 25%;
      padding-right: 2.96em;
    }
    &-button{
      border-radius: 1em;
      background: $primary;
      width: 3.5em;
      height: 3.5em;
      @include center;
      transform: translateY(-40%);
      svg{
        width: 1.5em;
        height: auto;
      }
      &:hover{
        background: darken($primary,10%);
      }
      &-prev{
        left: 0;
      }
      &-next{
        right: 0;
      }
    }
  }
}


@media screen and (max-width: 992px){
  ._first-section{
    margin-top: -4.875em;
  }

  .catalog-item{
    &__grid{
      margin-top: 1.5em;
      grid-template-columns: 100%;
      gap: 1.5em;
    }
  }

  .gallery{
    &__small{
      order: 2;
      margin: 0 0 0 1em;
    }
  }

  .catalog-info{
    max-width: 100%;
    gap: 1.5em;
  }

  .props{
    max-width: 26em;
  }

  .catalog-detail{
    padding-top: 2.5em;
    &__nav{
      margin-top: 2em;
      justify-content: unset;
    }
  }

  .tabs{
    justify-content: center;
    padding: 1.5em;
  }

  ._tab{
    span{
      font-size: 1em;
    }
  }

  .product-props{
    @include grid100gap(2em);
    &__group{
      width: 100%;
    }
  }

  .product-slider{
    .swiper-slide{
      width: 50%;
    }
  }
}


@media screen and (max-width: 576px){
  ._first-section{
    margin-top: -3.8125em;
  }

  .gallery{
    height: unset;
    flex-direction: column;
    &__small{
      width: 100%;
      padding: 0 2.62em;
      margin: 1em 0 0;
      .swiper-slide{
        width: 5.5em;
        height: 5em;
        padding: 0 .5em 0 0;
      }
    }
    &__big{
      .gallery__img{
        height: 23em;
      }
    }
    .swiper{
      height: unset;
      &-button{
        &-prev,
        &-next{
          top: 50%;
          transform: translateY(-50%);
          bottom: unset;
          img{
            transform: rotate(-90deg);
          }
        }
        &-prev{
          left: 0;
        }
        &-next{
          left: unset;
          right: 0;
        }
      }
    }
  }

  .props{
    &__row{
      grid-template-columns: 12.5em calc(100% - 13.5em);
    }
  }

  .catalog-detail{
    overflow: hidden;
    &__nav{
      margin-top: 1em;
    }
  }

  .tabs{
    flex-direction: column;
  }

  ._tab{
    @include lastM(0 0 1em);
    &:after{
      display: none;
    }
  }

  .product-props{
    padding: 0;
  }

  .product-slider{
    .swiper {
      &-container{
        overflow: unset;
      }
      &-slide {
        width: 85%;
      }
      &-button{
        display: none;
      }
    }
  }
}
